import s from 'components/Filter/Filter.module.css';

import shortId from 'short-id';
import PropTypes from 'prop-types';

import { useSelector, useDispatch } from 'react-redux';
import { setFilter, resetFilter } from 'store/actions/actions-filter';
import { ReactComponent as ReactSprite } from 'images/sprite.svg';

function Filter({ btnClass, onBlockedFilter, renderBlocked, searchHelper }) {
  const radioInpAllId = shortId.generate();
  const radioInpBlockedId = shortId.generate();

  const filter = useSelector(state => state.filter);
  const dispatch = useDispatch();

  const searchContactsHandler = e => {
    dispatch(setFilter({ value: e.currentTarget.value }));
    searchHelper();
  };

  return (
    <div className={s.wrapper}>
      <ReactSprite />
      <div className={s.renderFilter}>
        <label
          htmlFor={radioInpAllId}
          className={s[renderBlocked ? 'renderLabel' : 'renderLabelChecked']}
        >
          All
          <input
            name="renderList"
            id={radioInpAllId}
            className={s.radioInput}
            value="all"
            type="radio"
            checked={!renderBlocked}
            onChange={onBlockedFilter}
          ></input>
          <span className={s.renderList}></span>
        </label>
        <label
          htmlFor={radioInpBlockedId}
          className={s[renderBlocked ? 'renderLabelChecked' : 'renderLabel']}
        >
          Blocked
          <input
            name="renderList"
            id={radioInpBlockedId}
            className={s.radioInput}
            value="blocked"
            type="radio"
            checked={renderBlocked}
            onChange={onBlockedFilter}
          ></input>
          <span className={s.renderList}></span>
        </label>
      </div>

      <div className={s.filter}>
        <svg className={s.filterIcon}>
          <use href={'#icon-search'}></use>
        </svg>
        <input
          className={s.filterInput}
          name="filter"
          type="text"
          value={filter}
          onChange={searchContactsHandler}
          autoComplete="off"
        ></input>
        <button className={s[btnClass]} onClick={() => dispatch(resetFilter())}>
          {' '}
          X{' '}
        </button>
      </div>
    </div>
  );
}

Filter.propTypes = {
  btnClass: PropTypes.string.isRequired,
  onBlockedFilter: PropTypes.func.isRequired,
  renderBlocked: PropTypes.bool.isRequired,
  searchHelper: PropTypes.func,
};

export { Filter };
