import { createReducer } from '@reduxjs/toolkit';

import {
  addContact,
  deleteContact,
  toggleContactBlock,
} from '../actions/actions-contacts';
import shortId from 'short-id';

export const contactsReducer = createReducer([], {
  [addContact.type]: (state, action) => [
    ...state,
    { ...action.payload.contact, id: shortId.generate(), isBlocked: false },
  ],
  [deleteContact.type]: (state, action) =>
    state.filter(el => el.id !== action.payload.id),
  [toggleContactBlock.type]: (state, action) =>
    state.map(el => {
      if (el.id === action.payload.id) {
        el = {
          ...el,
          isBlocked: !el.isBlocked,
        };
      }
      return el;
    }),
});
